import React from "react";

import Row from "./Row"

export default class Table extends React.Component{

	constructor(props){
		super(props);
		this.state = {kk:"oo"}
	}

	getSortOrder(columnName){
		if(columnName === "firstName"){
			this.setState({sortFirstNameAsc:(!this.state.sortFirstNameAsc)});
			return !this.state.sortFirstNameAsc;
		}else if(columnName === "lastName"){
			this.setState({sortLasNameAsc:(!this.state.sortLastNameAsc)});
			return !this.state.sorLastNameAsc;
		}else if(columnName === "age"){
			this.setState({sortAgeAsc:(!this.state.sortAgeAsc)});
			return !this.state.sortAgeAsc;
		}else if(columnName === "favouriteColour"){
			this.setState({sortFavouriteColourAsc:(!this.state.sortFavouriteColourAsc)});
			return !this.state.sortFavouriteColourAsc;
		}else if(columnName === "hobby"){
			this.setState({sortHobbyAsc:(!this.state.sortHobbyAsc)});
			return !this.state.sortHobbyAsc;
		}
	}

	render(){
		 
		return(
			 <table>
		        <thead>
		          <tr>
		            <th className="input-margin input-display">Firstname  
		            	<span>
			            	<a href="" className={this.state.sortFirstNameAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("firstName",this.getSortOrder("firstName"))}} >
				            	<i className="icon ion-android-arrow-up"></i>
				            </a>
							<a href="" className={!this.state.sortFirstNameAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("firstName",this.getSortOrder("firstName"))}} >
				            	<i className="icon ion-android-arrow-down"></i>
				            </a>
			            </span>
		            </th>
		            <th className="input-margin input-display">Lastname  
		            	<span>
			            	<a href="" className={this.state.sortLastNameAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("lastName",this.getSortOrder("lastName"))}} >
				            	<i className="icon ion-android-arrow-up"></i>
				            </a>
							<a href="" className={!this.state.sortLastNameAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("lastName",this.getSortOrder("lastName"))}} >
				            	<i className="icon ion-android-arrow-down"></i>
				            </a>
			            </span>
		            </th>
		            <th className="input-margin input-display" >Age
		            	<span>
			            	<a href="" className={this.state.sortAgeAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("age", this.getSortOrder("age"))}} >
				            	<i className="icon ion-android-arrow-up"></i>
				            </a>
										<a href="" className={!this.state.sortAgeAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("age", this.getSortOrder("age"))}} >
				            	<i className="icon ion-android-arrow-down"></i>
				            </a>
			            </span>
		            </th>
		            <th className="input-margin input-display">Fav Colour
		            	<span>
			            	<a href="" className={this.state.sortColourAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("colour",this.getSortOrder("colour"))}} >
				            	<i className="icon ion-android-arrow-up"></i>
				            </a>
										<a href="" className={!this.state.sortColourAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("colour",this.getSortOrder("colour"))}} >
				            	<i className="icon ion-android-arrow-down"></i>
				            </a>
			            </span>
		            </th>
							<th className="input-margin input-display">Hobby
								<span>
									<a href="" className={this.state.sortHobbyAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("hobby",this.getSortOrder("hobby"))}} >
										<i className="icon ion-android-arrow-up"></i>
									</a>
									<a href="" className={!this.state.sortHobbyAsc ? "show":"hide"} onClick={(e) => {e.preventDefault();this.props.sort("hobby",this.getSortOrder("hobby"))}} >
										<i className="icon ion-android-arrow-down"></i>
									</a>
								</span>
							</th>
		          <th className="input-margin input-display"></th>
							</tr>
		        </thead>
		        <tbody>
		        	{this.props.rows.map( (row, index) => <Row key={row.id} personId={row.id} firstName={row.first_name} lastName={row.last_name} age={row.age} favouriteColour={row.favourite_colour} hobby={row.hobby} deletePerson={ () => {this.props.deletePerson(row.id)}}/>)}
		        	
		        </tbody>
		        <tfoot></tfoot>
		      </table>
		);

	}
}


